<template>
  <div class="auth-container">
    <div class="auth-card">
      <h1>{{ isLoginMode ? '登录' : '注册' }}</h1>

      <form @submit.prevent="handleSubmit">
        <div class="form-group">
          <label for="username">用户名</label>
          <input
            v-model="formData.username"
            type="text"
            id="username"
            required
          />
          <span class="error" v-if="errors.username">{{ errors.username }}</span>
        </div>

        <div class="form-group">
          <label for="password">密码</label>
          <input
            v-model="formData.password"
            type="password"
            id="password"
            required
          />
          <span class="error" v-if="errors.password">{{ errors.password }}</span>
        </div>

        <div class="form-group" v-if="!isLoginMode">
          <label for="email">邮箱 (可选)</label>
          <input
            v-model="formData.email"
            type="email"
            id="email"
          />
        </div>

        <button type="submit" :disabled="isSubmitting">
          {{ isLoginMode ? '登录' : '注册' }}
          <span v-if="isSubmitting" class="spinner"></span>
        </button>

        <p class="toggle-mode">
          {{ isLoginMode ? '没有账号？' : '已有账号？' }}
          <a href="#" @click.prevent="toggleMode">{{ isLoginMode ? '注册' : '登录' }}</a>
        </p>
      </form>
    </div>
  </div>
</template>
<script setup>
import { ref } from 'vue'
import { useRouter } from 'vue-router'
import { login, register } from '@/api/request'
import { useUserStore } from '@/stories/user'


const router = useRouter()
const userStore = useUserStore()


const isLoginMode = ref(true)
const isSubmitting = ref(false)

const formData = ref({
  username: '',
  password: '',
  email: ''
})

const errors = ref({
  username: '',
  password: ''
})

const toggleMode = () => {
  isLoginMode.value = !isLoginMode.value
  resetForm()
}

const resetForm = () => {
  formData.value = {
    username: '',
    password: '',
    email: ''
  }
  errors.value = {
    username: '',
    password: ''
  }
}

const validateForm = () => {
  let isValid = true

  if (!formData.value.username.trim()) {
    errors.value.username = '请输入用户名'
    isValid = false
  } else if (formData.value.username.length < 4) {
    errors.value.username = '用户名至少4个字符'
    isValid = false
  } else {
    errors.value.username = ''
  }

  if (!formData.value.password) {
    errors.value.password = '请输入密码'
    isValid = false
  } else if (formData.value.password.length < 6) {
    errors.value.password = '密码至少6个字符'
    isValid = false
  } else {
    errors.value.password = ''
  }

  return isValid
}

const handleSubmit = async () => {
  if (!validateForm()) return

  isSubmitting.value = true

  try {
    if (isLoginMode.value) {
      // 登录逻辑
      const user = await login(formData.value.username, formData.value.password)
      userStore.setUser(user)
      router.push('/dashboard')
    } else {
      // 注册逻辑
      const user = await register(formData.value)
      userStore.setUser(user)
      router.push('/dashboard')
    }
  } catch (error) {
    alert(error.message || '操作失败，请重试')
  } finally {
    isSubmitting.value = false
  }
}
</script>
<style scoped>
.auth-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  padding: 20px;
}

.auth-card {
  background: white;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
}

h1 {
  text-align: center;
  margin-bottom: 1.5rem;
  color: #333;
}

.form-group {
  margin-bottom: 1rem;
}

label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
  color: #555;
}

input {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1rem;
}

input:focus {
  outline: none;
  border-color: #4a90e2;
  box-shadow: 0 0 0 2px rgba(74, 144, 226, 0.2);
}

.error {
  color: #e74c3c;
  font-size: 0.875rem;
  margin-top: 0.25rem;
  display: block;
}

button {
  width: 100%;
  padding: 0.75rem;
  background-color: #4a90e2;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  margin-top: 1rem;
  transition: background-color 0.2s;
}

button:hover {
  background-color: #357ab8;
}

button:disabled {
  background-color: #a0c4f7;
  cursor: not-allowed;
}

.spinner {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s ease-in-out infinite;
  margin-left: 0.5rem;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.toggle-mode {
  text-align: center;
  margin-top: 1rem;
  color: #666;
}

.toggle-mode a {
  color: #4a90e2;
  text-decoration: none;
}

.toggle-mode a:hover {
  text-decoration: underline;
}
</style>