import { createApp } from 'vue';
import BlogPlatform from "@/Blog-platform.vue";
import router from './router'; // 引入路由配置
import ElementPlus from 'element-plus'; // 引入 Element Plus UI 库
import 'element-plus/dist/index.css'; // 引入 Element Plus 的样式文件
import { createPinia } from 'pinia'

// 创建 Vue 应用实例
const app = createApp(BlogPlatform);

// 使用插件
app.use(router); // 注册路由插件
app.use(ElementPlus); // 注册 Element Plus 插件
app.use(createPinia())


// 挂载应用到 DOM 元素
app.mount('#app'); // 将 Vue 应用挂载到 id 为 'app' 的 DOM 元素上
