import axios from 'axios'; // 导入 axios 库，用于发起 HTTP 请求

// 创建 axios 实例
const instance = axios.create({
    baseURL: 'http://localhost:8081', // 设置请求的基础 URL
    timeout: 10000, // 请求超时时间（毫秒）
});

// 定义一个异步函数，用于获取客户数据
export async function login(username, password) {
  try {
    const response = await instance.post('/user/login', {
      username,
      password
    }, {
      headers: {
        'Content-Type': 'application/json'
      }
    })

    // 存储token到localStorage
    if (response.data.token) {
      localStorage.setItem('authToken', response.data.token)
    }

    return response.data
  } catch (error) {
    console.error('登录失败:', error.response?.data || error.message)
    throw new Error(error.response?.data?.message || '登录失败，请检查用户名和密码')
  }
}

/**
 * 用户注册
 * @param {Object} userData - 用户注册数据
 * @param {string} userData.username - 用户名
 * @param {string} userData.password - 密码
 * @param {string} [userData.email] - 邮箱（可选）
 * @returns {Promise} 包含注册结果的Promise
 */
export async function register(userData) {
  try {
    const response = await instance.post('/user/doRegister', userData, {
      headers: {
        'Content-Type': 'application/json'
      }
    })
    return response.data
  } catch (error) {
    console.error('注册失败:', error.response?.data || error.message)
    throw new Error(error.response?.data?.message || '注册失败，请检查输入信息')
  }
}

